.App {
  text-align: center;
}

p strong {
  font-size: 2em;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  animation: colorCycle 8s infinite ease-in-out;
}

.subtitle {
  margin-top: 0;
  margin-bottom: 48px;
  font-size: 0.8em;
  white-space: pre-line;
  line-height: 2;
}

.App-link {
  background-color: #61dafb;
  color: #ffffff;
  padding: 16px;
  border-radius: 4px;
  margin-top: 32px;
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.App-link:hover {
  opacity: 0.95;
}

input {
  outline: none;
  border-radius: 4px;
  border: none;
  padding: 8px;
  font-size: 0.8em;
  text-align: center;
}

@keyframes colorCycle {
  0% {
    background-color: #282C34;
    color: #ffeeee;
  }
  50% {
    background-color: #322D31;
    color: #eeffff;
  }
  100% {
    background-color: #282C34;
    color: #ffdddd;
  }
}
